.banner{
  &__wrapper{
    display: flex;
    align-items: center;

    height: 155px;
    padding: 0 60px;

    background: #FFFFFF;
    border-radius: 8px;
  }
  &__img{
    max-height: 180px;
    flex-shrink: 0;
    img{
      height: 100%;

      object-fit: contain;
    }
  }
  &__content{
    max-width: 410px;

    margin: 0 15px;
  }
  &__title{
    font-family:'Montserrat', sans-serif;
    font-weight: 600;

    font-size: 31px;
    line-height: 38px;

    color: #23222F;
  }
  &__text{
    margin-top: 4px;

    font-family: 'Acrom', sans-serif;
    font-size: 14px;
    line-height: 115%;

    letter-spacing: -0.01em;

    color: #23222F;
  }
  &__footer{
    margin-left: auto;
  }
  &__btn{
    width: 90px;
    height: 42px;

    border: 1px solid #23222F;
    border-radius: 10px;
  }
}