
.btn{
  -webkit-appearance: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;

  &-light-blue{
    background: #D8E9FE;
    border-radius: 8px;

    font-family: 'Acrom', sans-serif;
    font-size: 14px;
    letter-spacing: -0.01em;
    color: #003BB7;

  }
  &-blue{
    font-family: 'Acrom', sans-serif;
    font-weight: 500;
    font-size: 14px;

    letter-spacing: -0.01em;

    color: #FFFFFF;

    background: #4060FA;
    border-radius: 8px;
  }
}

.page-wrapper{
  display: flex;

  background: #F5F5FB;

  min-height: 100vh;
  &__aside{
    position: sticky;
    width: 216px;
    height: 100vh;

    top: 0;
    left: 0;
  }
  &__content{
    padding: 30px 40px 45px 40px;

    width: 100%;
  }
  &__banner{
    margin: 30px 0 40px 0;
  }
  &__block + &__block{
    margin-top: 30px;
  }
}