.single-article{
  &__wrapper{
    padding: 15px 23px;
    width: 100%;
    max-width: 780px;


    background: #FFFFFF;
    border-radius: 8px;
  }
  &__header{
    width: 100%;
  }
  &__img{
    height: 400px;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 8px;
    &-text{
      font-family: "Acrom", sans-serif;
      font-size: 14px;

      color: #898989;
    }
  }
  &__content{
    margin-top: 15px;
  }
  &__title{
    font-family: 'Merriweather', sans-serif;
    font-weight: 700;
    font-size: 42px;
    line-height: 110%;
    color: #2A2A2A;
  }
  &__desc{
    margin-top: 15px;
    p {
      margin-bottom: 10px;
      font-family: 'Acrom', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 125%;

      color: #000000;
    }
    blockquote{
      font-family: 'Acrom', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 125%;

      margin: 30px 0;
      color:#083BA6;
    }
  }
  &__text{
  }
}