*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: 'HelveticaNeue';
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Acrom';
  src: url("../fonts/Acrom/Acrom-Medium/Acrom-Medium.woff2") format("woff2"), url("../fonts/Acrom/Acrom-Medium/Acrom-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Acrom';
  src: url("../fonts/Acrom/Acrom-Regular/Acrom-Regular.woff2") format("woff2"), url("../fonts/Acrom/Acrom-Regular/Acrom-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Acrom';
  src: url("../fonts/Acrom/Acrom-Bold/Acrom-Bold.woff2") format("woff2"), url("../fonts/Acrom/Acrom-Bold/Acrom-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Merriweather';
  src: url("../fonts/Merriweather/Merriweather-Bold/Merriweather-Bold.woff2") format("woff2"), url("../fonts/Merriweather/Merriweather-Bold/Merriweather-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat/Montserrat-SemiBold/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat/Montserrat-SemiBold/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

.btn {
  -webkit-appearance: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.btn-light-blue {
  background: #D8E9FE;
  border-radius: 8px;
  font-family: 'Acrom', sans-serif;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #003BB7;
}

.btn-blue {
  font-family: 'Acrom', sans-serif;
  font-weight: 500;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #FFFFFF;
  background: #4060FA;
  border-radius: 8px;
}

.page-wrapper {
  display: flex;
  background: #F5F5FB;
  min-height: 100vh;
}

.page-wrapper__aside {
  position: sticky;
  width: 216px;
  height: 100vh;
  top: 0;
  left: 0;
}

.page-wrapper__content {
  padding: 30px 40px 45px 40px;
  width: 100%;
}

.page-wrapper__banner {
  margin: 30px 0 40px 0;
}

.page-wrapper__block + .page-wrapper__block {
  margin-top: 30px;
}

.aside {
  height: 100%;
  width: 100%;
}

.aside__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 30px 25px 50px 25px;
  overflow-y: auto;
  background: linear-gradient(179.35deg, #64B2D8 8.38%, #4D93EC 76.66%);
}

.aside__content {
  margin-top: 50px;
}

.aside__footer {
  margin-top: auto;
  padding-top: 20px;
  width: 100%;
}

.aside__btn {
  height: 40px;
  max-width: 165px;
  flex-shrink: 0;
}

.aside-logo {
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.aside-logo__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.aside-nav__item-has-child {
  position: relative;
}

.aside-nav__item-has-child.active > .aside-nav__link::after {
  transform: rotate(180deg);
}

.aside-nav__item-has-child > .aside-nav__link::after {
  content: '';
  position: relative;
  width: 10px;
  height: 10px;
  display: block;
  margin-left: 10px;
  background: url("../img/arrow-white.svg") center center/contain no-repeat;
  transition: .3s all linear;
}

.aside-nav__item-has-child > .aside-nav__link.active::after {
  background: url("../img/arrow-blue.svg") center center/contain no-repeat;
}

.aside-nav__item.active > .aside-nav__list {
  display: block;
}

.aside-nav__list .aside-nav__list {
  padding-left: 40px;
  display: none;
}

.aside-nav__list .aside-nav__list .aside-nav__list {
  padding-left: 12px;
}

.aside-nav__list .aside-nav__list .aside-nav__list .aside-nav__link {
  color: #CDEEFF;
}

.aside-nav__link {
  display: flex;
  align-items: center;
  color: #fff;
  padding: 6.5px 0;
}

.aside-nav__link-text {
  font-family: 'Acrom', sans-serif;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: inherit;
}

.aside-nav__link-icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}

.aside-nav__link-icon svg {
  width: 100%;
  height: 100%;
}

.page-list__item + .page-list__item {
  margin-top: 8px;
}

.page-list__link {
  padding: 10px 12px;
}

.page-list__link.active {
  background: #fff;
  border-radius: 8px;
  color: #126DE9;
}

.page-list__link.active svg path {
  fill: #126DE9;
}

.page-list__link-text {
  font-weight: 500;
  color: inherit;
  margin-left: 10px;
}

.section__title {
  font-family: 'Acrom', sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 125%;
  color: #000;
}

.section__header + .section__content {
  margin-top: 15px;
}

.banner__wrapper {
  display: flex;
  align-items: center;
  height: 155px;
  padding: 0 60px;
  background: #FFFFFF;
  border-radius: 8px;
}

.banner__img {
  max-height: 180px;
  flex-shrink: 0;
}

.banner__img img {
  height: 100%;
  object-fit: contain;
}

.banner__content {
  max-width: 410px;
  margin: 0 15px;
}

.banner__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 31px;
  line-height: 38px;
  color: #23222F;
}

.banner__text {
  margin-top: 4px;
  font-family: 'Acrom', sans-serif;
  font-size: 14px;
  line-height: 115%;
  letter-spacing: -0.01em;
  color: #23222F;
}

.banner__footer {
  margin-left: auto;
}

.banner__btn {
  width: 90px;
  height: 42px;
  border: 1px solid #23222F;
  border-radius: 10px;
}

.card-grid__wrapper {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 40px 25px;
  grid-auto-rows: 230px;
}

.card-grid__wrapper_small {
  grid-auto-rows: 200px;
}

.card-grid__item_column-medium {
  grid-column-end: span 2;
}

.card-grid__footer {
  margin-top: 35px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.card-grid__footer_margin-small {
  margin-top: 15px;
}

.card-grid__footer_position-center {
  justify-content: center;
}

.card-grid__link {
  font-family: 'Acrom', sans-serif;
  font-size: 14px;
  letter-spacing: -0.01em;
  color: #126DE9;
}

.card-grid__btn {
  max-width: 195px;
  height: 45px;
}

.card-item__wrapper {
  position: relative;
  height: 100%;
  background: #fff;
  border-radius: 8px;
}

.card-item__header {
  height: 65%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  position: relative;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  overflow: hidden;
}

.card-item__header_size-small {
  padding-top: 10px;
  height: 115px;
}

.card-item__header-stars {
  position: absolute;
  left: 9px;
  top: 7px;
}

.card-item__header-footer {
  position: absolute;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: flex-end;
}

.card-item__stars {
  display: grid;
  grid-template-columns: repeat(5, minmax(0, 12px));
  gap: 1px;
}

.card-item__star {
  width: 12px;
  height: 12px;
}

.card-item__star-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.card-item__category {
  width: 32px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #9EC6F7;
}

.card-item__category-icon {
  width: 14px;
  height: 14px;
}

.card-item__tags {
  display: flex;
  align-items: center;
  margin-left: 4px;
}

.card-item__tag + .card-item__tag {
  margin-left: 4px;
}

.card-item__tag {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 18px;
  border-radius: 10px;
}

.card-item__tag_color-yellow {
  background: #F7ECD1;
}

.card-item__tag_color-purple {
  background: #F9D1EF;
}

.card-item__tag-text {
  font-family: 'Acrom', sans-serif;
  font-size: 12px;
  letter-spacing: -0.01em;
  color: #4F4F4F;
}

.card-item__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.card-item__img_size-full {
  object-fit: cover;
}

.card-item__content {
  padding: 15px 15px 20px 15px;
}

.card-item__content_size-small {
  padding: 8px 10px 0px 10px;
}

.card-item__title {
  font-family: "Merriweather", sans-serif;
  font-weight: 700;
  color: #2A2A2A;
  font-size: 18px;
  line-height: 23px;
}

.card-item__title_position-center {
  text-align: center;
}

.card-item__btn {
  position: absolute;
  bottom: -17.5px;
  left: calc(50% - 67.5px);
  width: 135px;
  height: 35px;
}

.single-article__wrapper {
  padding: 15px 23px;
  width: 100%;
  max-width: 780px;
  background: #FFFFFF;
  border-radius: 8px;
}

.single-article__header {
  width: 100%;
}

.single-article__img {
  height: 400px;
}

.single-article__img-item {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.single-article__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 8px;
}

.single-article__info-text {
  font-family: "Acrom", sans-serif;
  font-size: 14px;
  color: #898989;
}

.single-article__content {
  margin-top: 15px;
}

.single-article__title {
  font-family: 'Merriweather', sans-serif;
  font-weight: 700;
  font-size: 42px;
  line-height: 110%;
  color: #2A2A2A;
}

.single-article__desc {
  margin-top: 15px;
}

.single-article__desc p {
  margin-bottom: 10px;
  font-family: 'Acrom', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 125%;
  color: #000000;
}

.single-article__desc blockquote {
  font-family: 'Acrom', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 125%;
  margin: 30px 0;
  color: #083BA6;
}
