.aside{
  height: 100%;
  width: 100%;
  &__wrapper{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    width: 100%;
    height: 100%;

    padding: 30px 25px 50px 25px;

    overflow-y: auto;

    background: linear-gradient(179.35deg, #64B2D8 8.38%, #4D93EC 76.66%);
  }
  &__content{
    margin-top: 50px;
  }
  &__footer{
    margin-top:auto;
    padding-top: 20px;
    width: 100%;
  }
  &__btn{
    height: 40px;
    max-width: 165px;
    flex-shrink: 0;
  }
  &-logo{
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;
    &__img{
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }
  &-nav{
    &__item{
      &-has-child{
        position: relative;
        &.active{
          &>.aside-nav__link{
            &::after{
              transform: rotate(180deg);
            }
          }
        }
       &>.aside-nav__link{
         &::after{
           content: '';
           position: relative;
           width: 10px;
           height: 10px;
           display: block;
           margin-left: 10px;
           background: url("../img/arrow-white.svg") center center/contain no-repeat;

           transition: .3s all linear;
         }
         &.active{
           &::after{
             background: url("../img/arrow-blue.svg") center center/contain no-repeat;
           }
         }
       }
      }
      &.active{
        &>.aside-nav__list{
          display: block;
        }
      }
    }
    &__list{
      .aside-nav__list{
        padding-left: 40px;

        display: none;
        .aside-nav__list{
          padding-left: 12px;
          .aside-nav__link{
            color: #CDEEFF;
          }
        }
      }
    }
    &__link{
      display: flex;
      align-items: center;

      color:#fff;

      padding: 6.5px 0;
      &-text{
        font-family: 'Acrom', sans-serif;
        font-size: 14px;
        letter-spacing: -0.01em;

        color: inherit;
      }
      &-icon{
        width: 16px;
        height: 16px;

        flex-shrink: 0;
        svg{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
.page{
  &-list{
    &__item + &__item{
      margin-top: 8px;
    }
    &__link{
      padding: 10px 12px;
      &.active{
        background: #fff;
        border-radius: 8px;
        color: #126DE9;
        svg{
          path{
            fill: #126DE9;
          }
        }
      }
      &-text{
        font-weight: 500;
        color:inherit;
        margin-left: 10px;
      }
    }
  }
}